/* iPhone XR */
@media (min-width: 590px) and (max-width: 1000px) {
  html,
  body {
    height: 100%;
    background: #666 url("../../assets/images/e5c5ab2eba372858336d3f75d8ff-1434233.jpg") no-repeat fixed center;
    background-size: 80vw 65vh;
  }
  .cover-container {
    max-width:38em;
  }
}
/* iPhone SE & Samsung S8 */
@media (min-width: 359px) and (max-width: 590px){
  html,
  body {
    height: 100%;
    background: #666 url("../../assets/images/e5c5ab2eba372858336d3f75d8ff-1434233.jpg") no-repeat fixed center;
    background-size: 80vw 65vh;
  }
  .cover-container {
    max-width: 30em;
  }
}
/* Too Small */ 
@media (max-width: 300px){
  html { visibility: hidden;
  }
  body {
    visibility: hidden;
  }
  .cover-container {
    max-width: 30em;
    visibility: hidden;

  }
}

/*
 * Cover
 */
.cover {
  padding: 0 1.5rem;
}
.cover .btn-lg {
  padding: .75rem 1.25rem;
  font-weight: 700;
}


/*
 * Footer
 */
.mastfoot {
  color: rgba(255, 255, 255, .5);
}
